.game {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-direction: column;
      flex-direction: column;
  width: 50%;
  min-width: 400px;
  max-width: 700px;
  margin: auto;
}

.game a {
  color:  rgb(35, 154, 59);
  cursor: pointer;
}

.game a:hover {
  text-decoration: underline;
}

.game-board {
  padding: 10px;
  border: 2px solid #f1f1f1;
  border-radius: 3px;
  width: 100%;
}

.board-row {
  display: -ms-flexbox;
  display: flex;
  width: 100%;
}

.data {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  color: #c3c2c2;
}

.game-board .square {
  height: 100%;
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  background-color: rgb(235, 237, 240);
  margin: 3px;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  color: #d2d2d2;
  font-size: 32px;
  border-radius: 3px;
}
.game-board .square:after {
  content: "";
  display: block;
  padding-bottom: 100%;
}

.game-board .active {
  background-color: rgb(198, 228, 139);
  color: #f1f1f1;
}

.preview {
  margin: 40px;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  padding: 0px 30px;
}

.title {
  width: 60%;
  font-variant-caps: all-small-caps;
  color: #626262;
  margin: auto 0px;
}

.preview .final {
  margin-left: auto;
}

.preview .square {
  height: 20px;
  width: 20px;
  display: -ms-flexbox;
  display: flex;
  background-color: rgb(235, 237, 240);
  margin: 1px;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  color: #d2d2d2;
  font-size: 32px;
  border-radius: 1px;
}

.preview .active {
  background-color: rgb(198, 228, 139);
}

* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

@media only screen and (min-width: 576px;){
  .game-board {
    width: 100%;
  }
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



